
Template.__checkName("backward");
Template["backward"] = new Template("Template.backward", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("lengthIsOne"));
  }, function() {
    return [ "\n		", HTML.DIV({
      "class": "media-left media-middle"
    }, "\n			", HTML.I({
      "class": "fa fa-ban fa-2x"
    }), "\n		"), "\n	" ];
  }, function() {
    return [ "\n		", Spacebars.With(function() {
      return Spacebars.call(view.lookup("entry"));
    }, function() {
      return [ "\n			", HTML.DIV({
        "class": "media-left media-middle btn-backward"
      }, "\n				", HTML.I({
        "class": "fa fa-chevron-left fa-4x"
      }), "\n			"), "\n		" ];
    }), "\n	" ];
  });
}));
