
Template.__checkName("navSource");
Template["navSource"] = new Template("Template.navSource", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row text-center"
  }, "\n				", HTML.BUTTON({
    type: "button",
    "class": "btn btn-standard-2 btn-source"
  }, "\n					Woerter aus: ", Blaze.View("lookup:source", function() {
    return Spacebars.mustache(view.lookup("source"));
  }), "\n					", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "sourceFavourites");
  }, function() {
    return [ "\n						", HTML.SPAN({
      "class": "label label-default"
    }, Blaze.View("lookup:getSession", function() {
      return Spacebars.mustache(view.lookup("getSession"), "lengthFav");
    })), "\n					" ];
  }, function() {
    return [ "\n						", HTML.SPAN({
      "class": "label label-default"
    }, Blaze.View("lookup:getSession", function() {
      return Spacebars.mustache(view.lookup("getSession"), "lengthNotFav");
    })), "\n					" ];
  }), "\n				"), "\n				", Blaze.Unless(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "lengthFav");
  }, function() {
    return HTML.SPAN({
      "class": "label label-warning"
    }, "Kein Eintrag in Favoritenliste");
  }), "\n				", Blaze.Unless(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "lengthNotFav");
  }, function() {
    return HTML.SPAN({
      "class": "label label-warning"
    }, "Kein Eintrag in Nicht-Favoritenliste. Alle Woerter sind favorisiert.");
  }), "\n		");
}));
