
Template.__checkName("backwardLow");
Template["backwardLow"] = new Template("Template.backwardLow", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("lengthIsOne"));
  }, function() {
    return [ "\n	  	", HTML.I({
      "class": "fa fa-ban fa-4x"
    }), "\n	" ];
  }, function() {
    return [ "\n		", Spacebars.With(function() {
      return Spacebars.call(view.lookup("entry"));
    }, function() {
      return [ "\n			", HTML.DIV({
        "class": "btn-backward"
      }, "\n				", HTML.I({
        "class": "fa fa-chevron-left fa-4x"
      }), "\n			"), "\n		" ];
    }), "\n	" ];
  });
}));
