
Template.__checkName("registerEntry");
Template["registerEntry"] = new Template("Template.registerEntry", (function() {
  var view = this;
  return HTML.LI({
    "class": "story clearfix"
  }, "\n		", HTML.DIV({
    "class": "media"
  }, "\n			", HTML.DIV({
    "class": "media-body story-body"
  }, "\n\n					", HTML.H3({
    "class": "title"
  }, Blaze.View("lookup:term", function() {
    return Spacebars.mustache(view.lookup("term"));
  }), " ", HTML.I({
    "class": "preposition"
  }, Blaze.View("lookup:preposition", function() {
    return Spacebars.mustache(view.lookup("preposition"));
  }))), "\n					", HTML.SPAN({
    "class": "definition"
  }, "\n						", HTML.OL("\n							", Blaze.Each(function() {
    return Spacebars.call(view.lookup("definition"));
  }, function() {
    return [ "\n							", HTML.LI(Blaze.View("lookup:.", function() {
      return Spacebars.mustache(view.lookup("."));
    })), "\n							" ];
  }), "\n						"), "\n					"), "\n\n			"), "\n			", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("isFavourite"), Spacebars.dot(view.lookup("."), "_id"));
  }, function() {
    return [ "\n				", HTML.DIV({
      "class": "media-right media-middle btn-delete"
    }, "\n						", HTML.I({
      "class": "fa fa-trash-o fa-4x"
    }), "\n				"), "\n			" ];
  }, function() {
    return [ "\n				", HTML.DIV({
      "class": "media-right media-middle btn-insert"
    }, "\n						", HTML.I({
      "class": "fa fa-heart-o fa-4x"
    }), "\n				"), "\n			" ];
  }), "\n		"), "\n	");
}));
