
Template.__checkName("navSourceTrainer");
Template["navSourceTrainer"] = new Template("Template.navSourceTrainer", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row text-center"
  }, HTML.Raw('\n		<h5 class="widget-title">Quelle</h5>\n		'), HTML.UL({
    "class": "list-inline"
  }, "\n			", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "lengthFav");
  }, function() {
    return [ "\n				", HTML.LI(HTML.BUTTON({
      type: "button",
      "class": function() {
        return [ "btn btn-source-fav btn-standard-1 ", Blaze.If(function() {
          return Spacebars.dataMustache(view.lookup("getSession"), "sourceFavourites");
        }, function() {
          return "active";
        }) ];
      }
    }, "Favouriten ", HTML.SPAN({
      "class": "sup"
    }, "(", Blaze.View("lookup:getSession", function() {
      return Spacebars.mustache(view.lookup("getSession"), "lengthFav");
    }), ")"))), "\n			" ];
  }), "\n			", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "lengthNotFav");
  }, function() {
    return [ "\n				", HTML.LI(HTML.BUTTON({
      type: "button",
      "class": function() {
        return [ "btn btn-source-not-fav btn-standard-1 ", Blaze.Unless(function() {
          return Spacebars.dataMustache(view.lookup("getSession"), "sourceFavourites");
        }, function() {
          return "active";
        }) ];
      }
    }, "Nicht-Favouriten ", HTML.SPAN({
      "class": "sup"
    }, "(", Blaze.View("lookup:getSession", function() {
      return Spacebars.mustache(view.lookup("getSession"), "lengthNotFav");
    }), ")"))), "\n			" ];
  }), "\n		"), "\n	");
}));
