
Template.__checkName("nav");
Template["nav"] = new Template("Template.nav", (function() {
  var view = this;
  return HTML.NAV({
    "class": "navbar navbar-inverse nav-justified buffer-bar"
  }, "\n    ", HTML.DIV({
    "class": "container"
  }, "\n      ", HTML.DIV({
    "class": "navbar-header"
  }, "\n        ", HTML.Raw('<button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">\n          <span class="sr-only">Toggle navigation</span>\n          <span class="icon-bar"></span>\n          <span class="icon-bar"></span>\n          <span class="icon-bar"></span>\n        </button>'), "\n        ", Spacebars.include(view.lookupTemplate("atNavButton")), "\n      "), "\n      ", HTML.DIV({
    style: "height: 1px;",
    "aria-expanded": "false",
    id: "navbar",
    "class": "navbar-collapse collapse"
  }, "\n        ", HTML.UL({
    "class": "nav navbar-nav  text-center"
  }, "\n          ", HTML.LI({
    "class": function() {
      return Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "index"
      }));
    }
  }, HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "index");
    }
  }, "Index")), "\n          ", HTML.LI({
    "class": function() {
      return Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "trainer"
      }));
    }
  }, HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "trainer");
    }
  }, "Trainer")), "\n          ", HTML.LI({
    "class": function() {
      return Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "register"
      }));
    }
  }, HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "register");
    }
  }, "Register")), "\n          ", Blaze.Unless(function() {
    return Spacebars.call(view.lookup("surveySubmitted"));
  }, function() {
    return [ "\n          ", HTML.LI({
      "class": function() {
        return Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
          regex: "fragebogen"
        }));
      }
    }, HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("pathFor"), "fragebogen");
      }
    }, "Fragebogen")), "\n          " ];
  }), " ", Blaze.If(function() {
    return Spacebars.dataMustache(view.lookup("isInRole"), "admin");
  }, function() {
    return [ "\n          ", HTML.LI({
      "class": function() {
        return Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
          regex: "verwaltung"
        }));
      }
    }, HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("pathFor"), "verwaltung");
      }
    }, "Verwaltung")), "\n          " ];
  }), "\n        "), "\n      "), "\n    "), "\n  ");
}));
