
Template.__checkName("letterList");
Template["letterList"] = new Template("Template.letterList", (function() {
  var view = this;
  return [ HTML.H3(Blaze.View("lookup:letter", function() {
    return Spacebars.mustache(view.lookup("letter"));
  })), "\n	", Blaze.Each(function() {
    return Spacebars.call(view.lookup("entries"));
  }, function() {
    return [ "\n		", Spacebars.include(view.lookupTemplate("registerEntry")), "\n	" ];
  }) ];
}));
