
Template.__checkName("termDay");
Template["termDay"] = new Template("Template.termDay", (function() {
  var view = this;
  return [ HTML.Raw("<br>\n		"), HTML.DIV({
    "class": "text-center"
  }, "\n			", HTML.Raw('<p class="categories">\n				Dein Wort des Tages\n			</p>'), "\n			", Spacebars.With(function() {
    return Spacebars.call(view.lookup("termDay"));
  }, function() {
    return [ "\n				", HTML.H2({
      "class": "category-title"
    }, Blaze.View("lookup:term", function() {
      return Spacebars.mustache(view.lookup("term"));
    })), "\n				", HTML.DIV({
      "class": "category-definition"
    }, "\n					", HTML.OL("\n						", Blaze.Each(function() {
      return Spacebars.call(view.lookup("definition"));
    }, function() {
      return [ "\n						", HTML.LI(Blaze.View("lookup:.", function() {
        return Spacebars.mustache(view.lookup("."));
      })), "\n						" ];
    }), "\n					"), "\n				"), "\n				", HTML.H2(HTML.I({
      "class": "fa fa-heart-o"
    })), "\n			" ];
  }), "\n		") ];
}));
