
Template.__checkName("navStatistics");
Template["navStatistics"] = new Template("Template.navStatistics", (function() {
  var view = this;
  return HTML.DIV({
    "class": "container"
  }, "\n    ", HTML.DIV({
    "class": "row"
  }, "\n      ", HTML.UL({
    "class": "nav nav-tabs nav-justified"
  }, "\n        ", HTML.LI({
    role: "presentation",
    "class": function() {
      return Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "device"
      }));
    }
  }, HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "device");
    }
  }, "Device")), "\n        ", HTML.LI({
    role: "presentation",
    "class": function() {
      return Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "mode"
      }));
    }
  }, HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "mode");
    }
  }, "Mode")), "\n        ", HTML.LI({
    role: "presentation",
    "class": function() {
      return Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "status"
      }));
    }
  }, HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "status");
    }
  }, "Status")), "\n        ", HTML.LI({
    role: "presentation",
    "class": function() {
      return Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "total"
      }));
    }
  }, HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "total");
    }
  }, "Total")), "\n      "), "\n    "), "\n  ");
}));
