
Template.__checkName("forwardLow");
Template["forwardLow"] = new Template("Template.forwardLow", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("lengthIsOne"));
  }, function() {
    return [ "\n			", HTML.I({
      "class": "fa fa-ban fa-4x"
    }), "\n	" ];
  }, function() {
    return [ "\n		", Spacebars.With(function() {
      return Spacebars.call(view.lookup("entry"));
    }, function() {
      return [ "\n			", HTML.DIV({
        "class": "btn-forward"
      }, "\n				", HTML.SPAN(HTML.I({
        "class": "fa fa-chevron-right fa-4x"
      })), "\n			"), "\n		" ];
    }), "\n	" ];
  });
}));
