
Template.__checkName("navOverview");
Template["navOverview"] = new Template("Template.navOverview", (function() {
  var view = this;
  return HTML.DIV({
    "class": "container"
  }, "\n		", HTML.DIV({
    "class": "row text-center"
  }, "\n			", HTML.Raw("<br>"), "\n			", HTML.DIV({
    "class": "btn-group btn-group-lg",
    role: "group"
  }, "\n				", HTML.A({
    "class": function() {
      return [ "btn btn-default ", Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "statistik"
      })) ];
    },
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "statistik");
    },
    role: "button"
  }, "Statistik"), "\n				", HTML.A({
    "class": function() {
      return [ "btn btn-default ", Spacebars.mustache(view.lookup("isActiveRoute"), Spacebars.kw({
        regex: "feedback"
      })) ];
    },
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "feedback");
    },
    role: "button"
  }, "Feedback"), "\n			"), "\n			", HTML.Raw("<br>"), "\n			", HTML.Raw("<br>"), "\n		"), "\n	");
}));
