
Template.__checkName("letterCollapse");
Template["letterCollapse"] = new Template("Template.letterCollapse", (function() {
  var view = this;
  return HTML.DIV({
    "class": "panel-body"
  }, "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("entries"));
  }, function() {
    return [ "\n      ", Spacebars.include(view.lookupTemplate("registerEntry")), "\n    " ];
  }), "\n  ");
}));
