
Template.__checkName("modusDefinition");
Template["modusDefinition"] = new Template("Template.modusDefinition", (function() {
  var view = this;
  return HTML.DIV({
    "class": "jumbotron"
  }, "\n\n		", HTML.DIV({
    "class": "row"
  }, "\n			", HTML.DIV({
    "class": "media"
  }, "\n				", HTML.DIV({
    "class": "media-body"
  }, "\n					", Blaze.If(function() {
    return Spacebars.call(view.lookup("lengthIsOne"));
  }, function() {
    return HTML.SPAN({
      "class": "flag animate-box fadeInUp animated"
    }, "Letzter Eintrag in ", Blaze.If(function() {
      return Spacebars.dataMustache(view.lookup("getSession"), "sourceFavourites");
    }, function() {
      return "Favoriten";
    }, function() {
      return "Nicht-Favoriten";
    }));
  }), "\n				"), "\n				", HTML.Raw('<div class="media-right">\n					<div class="i-transparent">\n						<i class="fa fa-minus fa-4x"></i>\n					</div>\n				</div>'), "\n			"), "\n		"), "\n\n		", HTML.DIV({
    "class": "row"
  }, "\n			", HTML.DIV({
    "class": "text-center"
  }, "\n				", HTML.P({
    "class": "trainer-word-class fadeInUp animated"
  }, "\n					", Blaze.View("lookup:wordClass", function() {
    return Spacebars.mustache(view.lookup("wordClass"));
  }), "\n				"), "\n				", HTML.H2({
    "class": "trainer-title"
  }, Blaze.View("lookup:term", function() {
    return Spacebars.mustache(view.lookup("term"));
  }), Blaze.If(function() {
    return Spacebars.call(view.lookup("preposition"));
  }, function() {
    return [ " ", HTML.SPAN({
      "class": "trainer-preposition"
    }, HTML.I(Blaze.View("lookup:preposition", function() {
      return Spacebars.mustache(view.lookup("preposition"));
    }))) ];
  })), "\n				", HTML.Raw("<!-- DEFINITION -->"), "\n				", Blaze.Unless(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "revealed");
  }, function() {
    return [ "\n					", HTML.DIV({
      "class": "btn-reveal"
    }, "\n						", HTML.I({
      "class": "trainer-question fa fa-question fa-4x"
    }), "\n						", HTML.Comment(' <div class="label label-info">Gesucht: Wort</div> '), "\n					"), "\n				" ];
  }, function() {
    return [ "\n					", HTML.DIV({
      "class": "trainer-definition flipInX animated"
    }, "\n						", HTML.OL("\n							", Blaze.Each(function() {
      return Spacebars.call(view.lookup("definition"));
    }, function() {
      return [ "\n							", HTML.LI(Blaze.View("lookup:.", function() {
        return Spacebars.mustache(view.lookup("."));
      })), "\n							" ];
    }), "\n						"), "\n					"), "\n				" ];
  }), "\n			"), "\n		"), "\n\n	");
}));
