
Template.__checkName("footer");
Template["footer"] = new Template("Template.footer", (function() {
  var view = this;
  return HTML.FOOTER({
    "class": "text-center"
  }, "\n    ", HTML.DIV({
    "class": function() {
      return [ "container footer-content ", Blaze.If(function() {
        return Spacebars.call(view.lookup("currentUser"));
      }, function() {
        return "footer-border";
      }) ];
    }
  }, "\n      Chalimo Vokabeltrainer | ", Blaze.If(function() {
    return Spacebars.call(view.lookup("currentUser"));
  }, function() {
    return [ " ", HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("pathFor"), "faq");
      }
    }, "Dokumentation"), " | " ];
  }), HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "disclaimer");
    }
  }, "Disclaimer"), "\n    "), "\n  ");
}));
