
Template.__checkName("navMode");
Template["navMode"] = new Template("Template.navMode", (function() {
  var view = this;
  return HTML.DIV({
    "class": "row text-center"
  }, "\n			", HTML.BUTTON({
    type: "button",
    "class": "btn btn-standard-2 btn-mode"
  }, "Option: ", Blaze.View("lookup:mode", function() {
    return Spacebars.mustache(view.lookup("mode"));
  })), "\n	");
}));
