
Template.__checkName("feedbackClient");
Template["feedbackClient"] = new Template("Template.feedbackClient", (function() {
  var view = this;
  return HTML.DIV({
    "class": ""
  }, "\n  ", Blaze._TemplateWith(function() {
    return {
      collection: Spacebars.call("Data.Feedback"),
      id: Spacebars.call("insertFeedbackForm"),
      type: Spacebars.call("insert")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("autoForm"), function() {
      return [ "\n  ", HTML.FIELDSET("\n    ", Blaze._TemplateWith(function() {
        return {
          name: Spacebars.call("message")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("afQuickField"));
      }), "\n    ", HTML.DIV("\n      ", HTML.BUTTON({
        type: "submit",
        "class": "btn btn-standard-1"
      }, "Senden"), "\n      ", HTML.BUTTON({
        type: "reset",
        "class": "btn btn-standard-1"
      }, "Zuruecksetzen"), "\n    "), "\n  "), "\n  " ];
    });
  }), "\n");
}));
