
Template.__checkName("index");
Template["index"] = new Template("Template.index", (function() {
  var view = this;
  return HTML.DIV({
    "class": "container"
  }, "\n    ", HTML.DIV({
    "class": "row"
  }, "\n    ", HTML.SPAN({
    "class": "categories"
  }, Blaze.If(function() {
    return Spacebars.call(view.lookup("currentUser"));
  }, function() {
    return [ "Angemeldet als: ", Blaze.View("lookup:currentUserMail", function() {
      return Spacebars.mustache(view.lookup("currentUserMail"));
    }), " " ];
  })), "\n    "), "\n    ", HTML.DIV({
    "class": "row"
  }, "\n      ", Blaze.Unless(function() {
    return Spacebars.call(view.lookup("surveySubmitted"));
  }, function() {
    return [ "\n      ", HTML.BR(), "\n      ", HTML.DIV({
      "class": "archive-box"
    }, "\n        ", HTML.P("\n          Bitte fuelle den Fragebogen aus. Du hast bis zum 03. Juli 2016 dafuer Zeit. Nur mit ausgefuellten Fragebogen ist deine Teilnahme gueltig.\n        "), "\n        ", HTML.A({
      "class": "btn btn-standard-1 btn-question",
      href: function() {
        return Spacebars.mustache(view.lookup("pathFor"), "fragebogen");
      }
    }, "Fragebogen"), "\n      "), "\n      " ];
  }), "\n      ", HTML.Raw("<br>"), "\n\n    "), "\n    ", HTML.DIV({
    "class": "row"
  }, "\n      ", Spacebars.include(view.lookupTemplate("termDay")), "\n    "), HTML.Raw("\n    <br>\n    <br>\n    "), HTML.DIV({
    "class": "row"
  }, "\n      ", HTML.DIV({
    "class": "col-sm-8"
  }, "\n        ", HTML.DIV({
    "class": "row offset-1"
  }, "\n          ", Spacebars.include(view.lookupTemplate("faqWidget")), "\n        "), "\n        ", HTML.DIV({
    "class": "row offset-1"
  }, "\n          ", Spacebars.include(view.lookupTemplate("feedbackClient")), "\n        "), "\n      "), "\n      ", HTML.DIV({
    "class": "col-sm-4 "
  }, "\n        ", HTML.Raw('<p class="categories text-center">\n          Beliebte Woerter\n        </p>'), "\n        ", HTML.DIV({
    "class": "row"
  }, "\n          ", Spacebars.include(view.lookupTemplate("wordsUser")), "\n        "), "\n        ", HTML.DIV({
    "class": "row"
  }, "\n          ", Spacebars.include(view.lookupTemplate("wordsAll")), "\n        "), "\n      "), "\n    "), HTML.Raw("\n\n    <br>\n\n  "));
}));
