
Template.__checkName("vokabelDetail");
Template["vokabelDetail"] = new Template("Template.vokabelDetail", (function() {
  var view = this;
  return HTML.DIV({
    "class": "container"
  }, "\n		", Blaze.If(function() {
    return Spacebars.call(view.templateInstance().subscriptionsReady());
  }, function() {
    return [ "\n		    ", Spacebars.With(function() {
      return Spacebars.call(view.lookup("entry"));
    }, function() {
      return [ "\n		      ", HTML.H3(Blaze.View("lookup:term", function() {
        return Spacebars.mustache(view.lookup("term"));
      })), "\n		      ", HTML.P(Blaze.View("lookup:description", function() {
        return Spacebars.mustache(view.lookup("description"));
      })), "\n		    " ];
    }), "\n		  " ];
  }, function() {
    return [ "\n		      ", Spacebars.include(view.lookupTemplate("loading")), "\n		  " ];
  }), HTML.Raw("\n\n			<p>\n				noch weitere Details.\n				<ul>\n					<li>Fav button</li>\n					<li>Beispielsatz</li>\n					<li>Synonym</li>\n				</ul>\n			</p>\n\n	"));
}));
