
Template.__checkName("modusEingabe");
Template["modusEingabe"] = new Template("Template.modusEingabe", (function() {
  var view = this;
  return HTML.DIV({
    "class": "jumbotron"
  }, "\n\n		", Blaze.If(function() {
    return Spacebars.call(view.lookup("lengthIsOne"));
  }, function() {
    return HTML.SPAN({
      "class": "label label-warning text-center"
    }, "Letzter Eintrag in ", Blaze.If(function() {
      return Spacebars.dataMustache(view.lookup("getSession"), "sourceFavourites");
    }, function() {
      return "Favoriten";
    }, function() {
      return "Nicht-Favoriten";
    }));
  }), "\n\n		", HTML.DIV({
    "class": "media"
  }, "\n			", HTML.DIV({
    "class": "media-left btn-reveal"
  }, "\n					", Blaze.Unless(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "revealed");
  }, function() {
    return [ "\n						", Blaze.Unless(function() {
      return Spacebars.dataMustache(view.lookup("getSession"), "termRight");
    }, function() {
      return [ "\n							", HTML.I({
        "class": "trainer-question fa fa-question fa-4x"
      }), "\n						" ];
    }, function() {
      return [ "\n							", HTML.I({
        "class": "trainer-exclamation fa fa-thumbs-o-up fa-4x"
      }), "\n						" ];
    }), "\n					" ];
  }, function() {
    return [ "\n						", HTML.I({
      "class": "trainer-exclamation fa fa-exclamation fa-4x"
    }), "\n					" ];
  }), "\n			"), "\n			", HTML.DIV({
    "class": "media-body media-middle"
  }, "\n				", Blaze.Unless(function() {
    return Spacebars.dataMustache(view.lookup("getSession"), "revealed");
  }, function() {
    return [ "\n					", Blaze.If(function() {
      return Spacebars.dataMustache(view.lookup("getSession"), "termWrong");
    }, function() {
      return [ "\n					", HTML.Comment(' <h2 class="trainer-title">{{ term }}{{#if preposition}} <span class="trainer-preposition"><i>{{ preposition }}</i></span>{{/if}}</h2> '), "\n						", HTML.P({
        "class": "categories"
      }, "Bisher richtig:"), "\n						", HTML.H2({
        "class": "trainer-title"
      }, Blaze.View("lookup:getSession", function() {
        return Spacebars.mustache(view.lookup("getSession"), "termCache");
      })), "\n					" ];
    }), "\n					", Blaze.If(function() {
      return Spacebars.dataMustache(view.lookup("getSession"), "termRight");
    }, function() {
      return [ "\n						", HTML.P({
        "class": "categories fadeIn animated"
      }, "Richtig!"), "\n						", HTML.H2({
        "class": "trainer-title fadeIn animated"
      }, Blaze.View("lookup:term", function() {
        return Spacebars.mustache(view.lookup("term"));
      }), Blaze.If(function() {
        return Spacebars.call(view.lookup("preposition"));
      }, function() {
        return [ " ", HTML.SPAN({
          "class": "trainer-preposition"
        }, HTML.I(Blaze.View("lookup:preposition", function() {
          return Spacebars.mustache(view.lookup("preposition"));
        }))) ];
      })), "\n					" ];
    }), "\n				" ];
  }, function() {
    return [ "\n				", HTML.P({
      "class": "categories fadeIn animated"
    }, Blaze.View("lookup:wordClass", function() {
      return Spacebars.mustache(view.lookup("wordClass"));
    })), "\n				", HTML.H2({
      "class": "trainer-title fadeIn animated"
    }, Blaze.View("lookup:term", function() {
      return Spacebars.mustache(view.lookup("term"));
    }), Blaze.If(function() {
      return Spacebars.call(view.lookup("preposition"));
    }, function() {
      return [ " ", HTML.SPAN({
        "class": "trainer-preposition"
      }, HTML.I(Blaze.View("lookup:preposition", function() {
        return Spacebars.mustache(view.lookup("preposition"));
      }))) ];
    })), "\n				" ];
  }), "\n			"), "\n		"), HTML.Raw('\n\n		<div class="input-group-lg form-wrapper">\n					<input autofocus="" type="text" name="term" id="term" class="form-control" placeholder="Das gesuchte Wort lautet...">\n		</div>\n\n		<br>\n		<br>\n		'), HTML.DIV({
    "class": "text-center "
  }, "\n			", HTML.DIV({
    "class": "trainer-definition flipInX animated"
  }, "\n				", HTML.OL("\n					", Blaze.Each(function() {
    return Spacebars.call(view.lookup("definition"));
  }, function() {
    return [ "\n					", HTML.LI(Blaze.View("lookup:.", function() {
      return Spacebars.mustache(view.lookup("."));
    })), "\n					" ];
  }), "\n				"), "\n			"), "\n		"), "\n	");
}));
