
Template.__checkName("wordsUser");
Template["wordsUser"] = new Template("Template.wordsUser", (function() {
  var view = this;
  return [ HTML.Raw('<h5 class="widget-title">Top 5 Persoenlich</h5>\n		'), HTML.DIV({
    "class": "widget"
  }, "\n			", Blaze.If(function() {
    return Spacebars.call(view.lookup("top5"));
  }, function() {
    return [ "\n				", HTML.UL("\n					", Blaze.Each(function() {
      return Spacebars.call(view.lookup("top5"));
    }, function() {
      return [ "\n					", HTML.LI(Blaze.View("lookup:vocabularyName", function() {
        return Spacebars.mustache(view.lookup("vocabularyName"));
      })), "\n					" ];
    }), "\n				"), "\n			" ];
  }, function() {
    return [ "\n				", HTML.DIV({
      "class": "categories text-center"
    }, "\n					Nicht genuegend Daten. Benutz' mich. ", HTML.BR(), HTML.BR(), "\n				"), "\n			" ];
  }), "\n		") ];
}));
